/* Edited for Threads Theme */

.item-relations-related {
	position: relative;
}

.item-relations-related .bx-controls-direction {
    text-align: center;
	margin-bottom: $sc-margin-lv3;
}
.item-relations-related h3 {
   // @extend .product-slider-title;
   text-align: left;
   margin-bottom: 46px;
}

.item-relations-related .global-views-star-rating {
    margin: 0 auto;
    display: inline-block;
}

// .item-relations-related-row {}

.item-relations-related-carousel-next {
	margin-left:$sc-margin-lv2;
	cursor:pointer;
	cursor:hand;
	position:absolute;
	right:0px;
	top: 30%;
	font-size: 3rem;
	color: #dadbda;
}

.item-relations-related-carousel-prev {
	cursor:pointer;
	cursor:hand;
	position:absolute;
	left:0px;
	top: 30%;
	font-size: 3rem;
	color: #dadbda;
}

.item-relations-related-carousel-next.disabled,
.item-relations-related-carousel-prev.disabled {
	display: none;
}

